import client from "./client";

export async function fetchProducts({ type, categoryId, limit }) {
  const params = {};
  let url = "/products";

  if (limit) {
    params.limit = limit;
  }

  if (type === "featured") {
    params.type = "featured";
  } else if (categoryId) { // Nu c categoryId, s dng endpoint dnh ring cho danh mc
    url = `/categories/${categoryId}/products`;
  }
  const response = await client.get(url, { params });
  return response.data;
}

export const fetchProduct = async (id) => {
  const response = await client.get(`/products/${id}`);
  return response.data;
};

export const fetchAllProducts = async () => {
  const response = await client.get("/products");
  return response.data;
}

export const createProductApi = async (payload) => {
  const res = await client.post(`/products`, payload);
  return res.data;
};

export const updateProductApi = async (id, payload) => {
  console.log(payload);
  const res = await client.patch(`/products/${id}`, payload);
  return res.data;
};

export const deleteProductApi = async (id) => {
  return client.delete(`/products/${id}`);
};

export const reduceStockApi = async (productId, stockQuantity) => {
  const res = await client.patch(`/products/${productId}`, { stockQuantity });
  console.log(res);
  return res.data;
};
